body {
  background-color: #757575;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.page {
  overflow: hidden;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.footer {
  margin-top: auto;
}

.content {
  min-height: calc(100vh - 52.5px - 70px);
  padding: 1.5rem 0;
}

.goods {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
}

.hiden {
  margin-bottom: 40px;
}

.header {
  background-color: black;
  position: fixed;
  width: 100%;
  z-index: 3;
}

.header__logo {
  color: #fff;
  font-size: 35px;
  font-style: italic;
  position: relative;
  margin-left: 1rem;
}

.nav__link {
  position: absolute;
  top: 0;
  right: 0;
  padding: 11.5px;
  margin-right: 8px;
  padding-left: 15px;
  padding-right: 15px;
  background-color: yellow;
  transform: skewX(-15deg);
  font-size: 20px;
  color: black;
  transition: all 0.3s linear;
}

.nav__link:hover {
  color: rgb(255, 255, 255);
  background-color: #000;
}

.slogan {
  text-align: center;
  color: #fff;
  top: 15px;
  left: 50%;
  transform: translateX(-50%);
  position: absolute;
  font-size: 20px;
}

.You {
  color: yellow;
  text-transform: uppercase;
}

.dark {
  color: black;
  font-weight: 900;
}

.frog {
  color: green;
}

.link__txt {
  color: white;
  font-weight: 500;
  opacity: 0.7;
  transition: opacity 0.2s linear;
}

.link__txt:hover {
  opacity: 1;
}

.card {
  background: #000;
}

.card-content {
  display: flex;
  flex-direction: column;
  color: #fff;
}

.card .card-content {
  padding: 0;
}

.card__up {
  border-bottom: 1px solid yellow;
  min-height: 150px;
  max-height: 150px;
  margin-bottom: 15px;
  padding: 24px;
}

.but {
  display: flex;
  justify-content: center;
}

.butn {
  margin-top: 15px;
  margin-bottom: 10px;
  padding: 15px;
  background-color: yellow;
  transform: skewX(-15deg);
  border: 3px solid yellow;
  cursor: pointer;
  font-weight: 500;
  transition: all 0.3s linear;
}

.butn:focus {
  background-color: yellow;
}

.butn:hover {
  background-color: #000;
  color: #fff;
  border: 3px solid black;
  font-weight: 500;
}

.price {
  display: flex;
  color: #fff;
  padding: 0 15px;
  align-items: center;
  justify-content: center;
}

.cart {
  cursor: pointer;

  background-color: #000;
  color: yellow;
  padding: 12px 15px 11px;
  border-radius: 3px;

  position: fixed;
  top: 0;
  right: 7rem;
  z-index: 4;

  transition: all 0.3s linear;
}

.cart:hover {
  background-color: yellow;
  color: #000;
}

.collection {
  margin: unset;
  background-color: #757575;
  border: 1px solid yellow;
}

.collection .collection-item {
  border-bottom: 1px solid yellow;
  background-color: #757575;
  color: #fff;
}

.collection .collection-item.active {
  background-color: #000;
  color: #fff;
}

.basket__list {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  background-color: #757575;
  width: 50rem;
  max-width: 95%;

  animation: show 500ms ease-in-out;
}

@keyframes show {
  from {
    top: 70%;
    opacity: 0;
  }
  to {
    top: 50%;
    opacity: 1;
  }
}

.close {
  color: yellow;
  cursor: pointer;
}

.delete {
  color: #000;
  cursor: pointer;
}

.item__plus {
  cursor: pointer;
  color: yellow;
  font-weight: 600;
  font-size: 20px;
}

.item__neg {
  cursor: pointer;
  color: yellow;
  font-weight: 600;
  font-size: 20px;
}

.unselectable {
  user-select: none;
}

.toast__container {
  position: fixed;
  top: 5rem;
  right: 1rem;
  max-width: 180px;

  animation: toast 6s ease-out;
}

@keyframes toast {
  0% {
    top: 30rem;
    opacity: 0;
  }
  20% {
    top: 5rem;
    opacity: 1;
  }
  100% {
    top: 5rem;
    opacity: 0;
  }
}

.toast {
  background-color: #000;
  color: yellow;
}

.buy {
  background-color: yellow;
  color: #000;
  font-weight: 600;
  padding: 10px;
  position: absolute;
  right: 20px;
  bottom: 5px;
  border: none;
  transform: skew(-15deg);
  transition: all 0.3s linear;
  cursor: pointer;
}

.buy:hover {
  background-color: #000;
  color: #fff;
}

.buy:focus {
  background-color: black;
  color: #fff;
  font-weight: 600;
}

.collection__item__act {
  line-height: 1.5rem;
  padding: 0 20px;
  margin: 0;
  background-color: #000;
  color: #fff;
  padding-bottom: 20px;
}

.total__price {
  padding: 15px;
}

@media (max-width: 768px) {
  .slogan {
    font-size: 15px;
  }
  .header__logo {
    top: 13px;
    font-size: 20px;
  }
  .header {
    height: 52.5px;
  }
}

@media (max-width: 493px) {
  .cart {
    top: unset;
    bottom: 2rem;
    right: 1rem;
  }
  .slogan {
    top: 18px;
    left: 53%;
    transform: translateX(-47%);
  }
  .header__logo {
    top: 13px;
  }
  .header {
    height: 52.5px;
  }
}

@media (max-width: 423px) {
  .slogan {
    top: 18px;
    font-size: 14px;
    left: 53%;
    transform: translateX(-47%);
  }
  .header__logo {
    top: 13px;
    font-size: 19px;
  }
  .header {
    height: 52.5px;
  }

  .cart {
    top: unset;
    bottom: 2rem;
    right: 1rem;
  }
}

@media (max-width: 423px) {
  .slogan {
    font-size: 12px;
  }
  .header__logo {
    font-size: 17px;
  }
}

@media (max-width: 359px) {
  .slogan {
    font-size: 10px;
  }
  .header__logo {
    font-size: 15px;
  }
}

@media (max-width: 310px) {
  .slogan {
    display: none;
  }
}
